import { Component, ReactNode } from 'react'
import './articleDetail.scss'
import moment from 'moment'

interface ArticleDetailProps{
    newsDetail:{
        commented?: number
        content?: string
        create_time?: number
        creator_name?: string
        readed?: number
        title?: string
        update_time?: number
    }
}
export default class ArticleDetail extends Component<ArticleDetailProps>{
    state={
        detail:{
            title:'',
            create_time:1641520819085,
            creator_name:'',
            readed:2039,
            content:'9'
        },
        newsDetail:this.props.newsDetail
    }
    render(): ReactNode {
        let newsDetail= this.props.newsDetail?this.props.newsDetail:this.state.detail
        return(
            newsDetail && <div className='articleDetail'>
                <div className='title'>{newsDetail.title}</div>
                <div className='flexb detail-info'>
                    <div className='flexl'>
                        <div>{moment(newsDetail.create_time).format('YYYYMMDD HH:mm')}</div>
                        <div className='detail-from'>{newsDetail.creator_name}</div>
                    </div>
                    <div> {newsDetail.readed}</div>
                </div>
                <div className='news-content'>
                    {newsDetail.content}
                </div>
            </div>
        )
    }
}